<script>
import debounce from "debounce";
export default {
  components: {},
  created() {
    // window.addEventListener("resize", this.onResize);
    window.addEventListener("scroll", this.onScroll);
    document.body.classList.add("enableScrolling");
  },
  mounted() {
    this.$nextTick(() => {
      if (this.$refs.messages) {
        this.messagesY = this.$refs.messages.getBoundingClientRect().top;
        this.lovesY = this.$refs.loves.getBoundingClientRect().top;
      }
    });
  },
  destroyed() {
    // window.removeEventListener("resize", this.onResize);
    window.removeEventListener("scroll", this.onScroll);
    document.body.classList.remove("enableScrolling");
  },
  data: () => {
    return {
      scrollY: 0,
      messagesY: 0,
      lovesY: 0
    };
  },
  methods: {
    onScroll() {
      debounce(() => {
        this.scrollY = window.scrollY;
      }, 100)();
    },
    gotoPost() {
      this.$router.push({ name: "Post" });
    },
    scrollToPosts() {
      window.scrollTo({ top: this.postsY, behavior: "smooth" });
    },
    scrollToMessages() {
      window.scrollTo({ top: this.messagesY, behavior: "smooth" });
    },
    scrollToLoves() {
      window.scrollTo({ top: this.lovesY, behavior: "smooth" });
    }
  },
  computed: {
    currentlyOn() {
      if (this.scrollY < this.messagesY) return "posts";
      else if (this.scrollY < this.lovesY) return "messages";
      else return "loves";
    }
  }
};
</script>

